<template>
	<div class="exbody" :style="exbodystyle">
		<van-nav-bar title="行情"  @click-right="onClickRight" />
		<van-row class="t">
			<van-col span="4" offset="3" class="extitle">资产名称</van-col>
			<van-col span="12" offset="3" class="extitle">
				<van-row type="flex">
					<van-col span="19">最新价</van-col>
					<van-col span="6">涨跌幅</van-col>
				</van-row>
			</van-col>
		</van-row>
		<van-list>
			<van-cell class="collcwor">
				<van-row>
					<van-col span="3" offset="2">HYT</van-col>
					<van-col span="12" offset="7">
						<van-row type="flex">
							<van-col span="16">$ {{hyt.price}}</van-col>
							<van-col span="6">
								<van-button type="danger" style="font-size: 1rem;width: 65px;height: 28px;" size="mini">{{hyt.profit*100 | numFilter}}%</van-button>
							</van-col>
						</van-row>
					</van-col>
				</van-row>

			</van-cell>
			<van-cell v-for="item in list" class="collcwor">
				<van-row >
					<van-col span="3" offset="2">{{item.name0.toUpperCase()}}</van-col>
					<van-col span="12" offset="7">
						<van-row type="flex">
							<van-col span="16">$ {{item.price}}</van-col>
							<van-col span="6">
								<van-button type="danger" style="font-size: 1rem;width: 65px;height: 28px;" size="mini" :style="item.ud<0?'background-color: green;border-color: green;height:25px':'height:25px'">{{item.ud}}%</van-button>
							</van-col>
						</van-row>
					</van-col>
				</van-row>

			</van-cell>
		</van-list>
		
		<van-actionsheet v-model="show" :actions="actions" cancel-text="取消" />
	</div>
</template>

<script>

	export default {
		name: 'ExchangeInfo',
		data() {
			return {
				show: false,
				actions: [{
					name: '添加行情',
					callback: this.addEx
				}],
				list: [],
				
				baseurlfont: 'http://47.75.169.147/fxjz/huobi/merged?symbol=',
				baseurlend: '&AccessKeyId=9a6e6d94-7d1ed76e-376e6d3a-32dee',
				inter: null,
				exbodystyle: 'height:500px',
				hyt:{
                     price:0,
					 up:0,
					 profit:0
				},
				hytinter:null

			}
		},
		methods: {
			onClickRight: function() {
				//this.show = true
			},
			addEx: function() {

				this.show = false
				this.$router.push({
					name: 'ExchangeAdd'
				});
			},getHytHq(){
				var that = this;
			    that.hytinter=setInterval(function(){
                                that.$axios.get(that.BaseUrl+'sysSetting/get')
								.then(function(res) {
									if(res.data.code=='200'){
                                         that.hyt.price=res.data.data.hytPrice;
                                         that.hyt.profit=res.data.data.profit;
									}
								})
								.catch(function(err) {
                                    
								})
				},3000);
				
			}

		},
		created() {
			var that = this;
			that.exbodystyle = 'height:' + window.innerHeight + 'px';
			
			//var exinfo = 'ETH/USDT-0%2CBTC/USDT-0%2CETC/USDT-0%2CLTC/USDT-0%2CBCH/USDT-0%2CADA/USDT-0%2COMG/USDT-0%2CDASH/USDT-0%2CIOTA/USDT-0%2CZEC/USDT-0%2CSTEEM/USDT-0%2CHB10/USDT-0';
			var exinfo = 'btc-eth-ltc-eos-etc-xrp-bch-ada-omg-dash-iota-zec-steem-hb10-ont-iost-pai-btm-dta';
			that.getHytHq();
			if(exinfo == null) {

			} else {
				var arr = exinfo.split('-');
				for(var i = 0; i < arr.length; i++) {
					
		/*			if(arr[i].split('-')[1] == 1) continue;
					console.log(i);
					var items = {};
					items.name0 = arr[i].split('-')[0].split('/')[0].toLowerCase();
					items.name1 = arr[i].split('-')[0].split('/')[1].toLowerCase();*/
					var items = {};
					items.name0 = arr[i];
					items.burl = this.baseurlfont + items.name0 + "usdt" + this.baseurlend;
					items.price = 0;
					items.ud = 0;
					this.list.push(items);
				}
				//开始轮询显示获取货币网数据
				this.inter = setInterval(function() {

					for(var i = 0; i < that.list.length; i++) {
						var index = i;
						(function(index) {
							that.$axios.get(that.list[i].burl)
								.then(function(res) {
									if(res.status == 200) {

										res = res.data;
										that.list[index].price = res.tick.close;
										that.list[index].ud = (((res.tick.close - res.tick.open) / res.tick.close) * 100).toFixed(2);
									}
								})
								.catch(function(err) {

								})
						})(index);
					}

				}, 2000);
			}

		},
		destroyed: function() {
			window.clearInterval(this.inter);
			window.clearInterval(this.hytinter);
		},
		
		// 保留小数点后两位的过滤器，尾数四舍五入

		filters: {
		  numFilter(value) {
		  	// 截取当前数据到小数点后两位
		    let realVal = Number(value).toFixed(2)
		    // num.toFixed(2)获取的是字符串
		    return Number(realVal)
		  }
		}
	}
</script>

<style>
	.exbody {
		background-color: #202437;
		font-family: "微软雅黑";
		font-size: 6%;
	}
	
	.exbody .extitle {
		font-size: 15px;
		height: 25px;
		line-height: 25px;
		color: #c7cce6;
		font-family: "微软雅黑";
	}
	
	.t {
		background-color: rgba(97, 104, 138, .15);
	}
	
	.exbody .van-cell {
		background-color: #202437;
		color: #c7cce6;
		padding: 5px 5px;
	}
	
	.exbody .van-hairline--bottom {
		background-color: #202437;
		color: #c7cce6;
		font-family: "微软雅黑";
	}

	.greenex {
		background-color: green;
		border-color: green;
	}
	
	.van-hairline .van-row{
		font-family: "微软雅黑";
	    font-size: 19px;
	    line-height: 20px;
	}
	
</style>


